'use'
import { cn } from "@/lib/utils";
import Link from "next/link";
import { usePathname } from "next/navigation";
export default function NavLink({
    href,
    children,
    className,
}: {
    href:string;
    children: React.ReactNode;
    className?: string;
}){
    const pathname = usePathname();
    return <Link href={href} className={cn("transition-colors text-sm duration-200 text-gray-600 hover:text-green-500",className)}>{children}</Link>
}